import React, { useState, useEffect, useCallback, useRef } from "react";
import {
  VStack,
  HStack,
  Center,
  Text,
  CheckCircleIcon,
  Pressable,
  Hidden,
  ChevronRightIcon,
  CheckIcon,
  Box,
} from "native-base";
import { useDispatch, useSelector } from "react-redux";
import moment from "moment-timezone";

import Colors from "../../../constants/Colors";
import { toFirstCase, vh, vw } from "../../../util";
import Card from "../properties/Card";
import ShadowButton from "../../../components/ShadowButton";
import * as planActions from "../../../store/actions/plan";
import * as settingsActions from "../../../store/actions/settings";
import { getUser } from "../../../constants/Config";
import ShadowModal from "../../../components/ShadowModal";

const useMountedState = () => {
  const mountedRef = useRef(false);
  const isMounted = useCallback(() => mountedRef.current, []);

  useEffect(() => {
    mountedRef.current = true;

    return () => {
      mountedRef.current = false;
    };
  }, []);

  return isMounted;
};

const SubscriptionPlans = ({ _showAlert, profile }) => {
  const dispatch = useDispatch();
  const isMounted = useMountedState();
  const [isConfirm, setIsConfirm] = useState(false);
  const [showDetails, setShowDetails] = useState(false);
  const [isCreating, setIsCreating] = useState(false);
  const [isCancel, setIsCancel] = useState(false);
  const [isSubscribed, setIsSubscribed] = useState(false);
  const [selectedPlan, setSelectedPlan] = useState(null);
  const { plan } = useSelector((state) => state.plan);

  const loadInit = useCallback(async () => {
    try {
      await dispatch(planActions.fetchPlans());
    } catch (err) {
      _showAlert("error", "something went wrong.");
    }
  }, [dispatch, _showAlert]);

  useEffect(() => {
    if (isMounted) {
      loadInit().then(() => {});
    }
  }, [dispatch, loadInit, isMounted]);

  const createSubscription = async () => {
    try {
      setIsCreating(true);
      const action = settingsActions.createSubscription(selectedPlan, profile);
      const result = await dispatch(action);
      // console.log(result);
      if (result && result._id) {
        _showAlert("success", "Thank you! Payment successful.");
        await dispatch(settingsActions.fetchCredits(1, 10, true));
        setIsConfirm(false);
        setSelectedPlan(null);
        setShowDetails(false);
      }
      setIsCreating(false);
    } catch (err) {
      console.log("Created Error", err.message);
      _showAlert("error", err.message);
      setIsCreating(false);
    }
  };

  const cancelSubscription = async () => {
    try {
      setIsCancel(true);
      const action = settingsActions.cancelSubscription();
      const { lastsub } = await dispatch(action);
      if (lastsub && lastsub.cancel && lastsub.cancel.status === "canceled") {
        _showAlert(
          "success",
          "Your request for Subscription cancellation is accepted and it will be cancelled at the end of the current billing cycle."
        );
      }
      setIsCancel(false);
      setIsConfirm(false);
      setSelectedPlan(null);
      setShowDetails(false);
    } catch (err) {
      console.log(err.message);
      setIsCancel(false);
      _showAlert("error", err.message);
    }
  };

  let subscribed = false;
  let subPlan = null;

  let subDetails = null;
  if (profile.lastsub && plan) {
    const lastsub = profile.lastsub;
    let status = "Active";
    const duration = moment
      .unix(lastsub.end)
      .diff(moment.unix(lastsub.start), "days");
    const start = moment.unix(lastsub.start).format("DD MMM YYYY");
    const end = moment.unix(lastsub.end).format("DD MMM YYYY");
    subPlan = plan && plan.find((p) => p.plan_id === lastsub.planid);
    const card = lastsub.brand
      ? toFirstCase(lastsub.brand) + " **** " + lastsub.card
      : "-";
    if (lastsub.cancel) {
      status = "Canceled";
      subscribed = false;
    } else {
      subscribed = true;
    }
    subDetails = {
      Name: subPlan.name,
      Type: subPlan.type.toUpperCase(),
      Billing: subPlan.period.toUpperCase(),
      "Billing Date": end,
      "Subscription Date": start,
      Status: status,
      Duration: duration + " days remaining",
      "Payment Method": card,
    };
  }

  return (
    <VStack>
      {showDetails && (
        <HStack space={8} justifyContent='flex-start'>
          <Card
            style={{
              marginBottom: vh(20),
              height: vh(430),
              width: "48%",
              paddingVertical: vh(10),
            }}
            title={"Subscription details"}
            rightElement={
              subscribed ? (
                <ShadowButton
                  fontSize={vw(13)}
                  style={{
                    height: vh(32),
                    minWidth: vw(80),
                    borderColor: "#00000026",
                  }}
                  color={"#666666"}
                  variant='outline'
                  label='Cancel Subscription'
                  onPress={async () => {
                    setIsConfirm(true);
                  }}
                />
              ) : (
                <></>
              )
            }
          >
            {subDetails ? (
              Object.keys(subDetails).map((d, idx) => (
                <HStack
                  key={"s-" + idx}
                  style={{
                    marginVertical: 11,
                    justifyContent: "space-between",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Poppins_medium",
                      fontSize: vw(15),
                      color: "#303030",
                    }}
                  >
                    {d}
                  </Text>
                  <Text
                    style={{
                      fontFamily: "Poppins_medium",
                      fontSize: vw(15),
                      color: "#919191",
                    }}
                  >
                    {subDetails[d]}
                  </Text>
                </HStack>
              ))
            ) : (
              <Center
                _text={{
                  fontFamily: "Poppins",
                  fontSize: vw(13),
                  color: "rgba(0,0,0,0.5)",
                }}
                style={{ height: 360 }}
              >
                Not Subscribed
              </Center>
            )}
          </Card>
          <Card
            style={{
              marginBottom: vh(20),
              height: vh(430),
              paddingVertical: vh(10),
              width: "48%",
            }}
            title={"Plan details"}
            rightElement={
              <ShadowButton
                fontSize={vw(13)}
                style={{
                  height: vh(32),
                  minWidth: vw(75),
                  borderColor: "#00000026",
                }}
                color={"#666666"}
                variant='outline'
                label='Back'
                onPress={() => {
                  setShowDetails(false);
                }}
              />
            }
          >
            {selectedPlan &&
              selectedPlan.details.map((d, idx) => (
                <HStack key={"p-" + idx} style={{ marginVertical: 11 }}>
                  <Center style={{ marginHorizontal: 22 }}>
                    <CheckIcon size={3} color='#31B131' />
                  </Center>
                  <Text
                    style={{
                      fontFamily: "Poppins_medium",
                      fontSize: vw(15),
                      color: "#303030",
                    }}
                  >
                    {d.title}
                  </Text>
                </HStack>
              ))}
          </Card>
        </HStack>
      )}

      {/* Large Screens Only */}
      {!showDetails && (
        <Hidden only={["sm", "base", "md"]}>
          <HStack space={4} flex={1} justifyContent='space-between'>
            {plan &&
              plan
                .filter((p) => {
                  if (profile.plan && profile.plan._id) {
                    return profile.plan.type === p.type;
                  } else {
                    return true;
                  }
                })
                .map((p, i) => {
                  let active = profile.plan._id === p._id;
                  let rd = 0;
                  if (profile.lastsub) {
                    const d = moment.unix(profile.lastsub.end);
                    const n = d.diff(moment(), "days");
                    rd = n;
                    if (profile.lastsub.cancel || n < 0) {
                      active = false;
                    }
                  }

                  return (
                    <VStack
                      key={"plan-" + i}
                      style={{
                        flex: 1,
                        // width: "80%", //vw(570),
                        borderWidth: vw(2),
                        borderColor: Colors.primary,
                        height: vh(140),
                        borderRadius: 10,
                        boxshadow: "0px 3px 10px #0000001A",
                        backgroundColor: active ? "white" : "transparent",
                        background: !active
                          ? "linear-gradient(137deg, #BA47F3 0%, #7D2BE9 100%)"
                          : "none",
                      }}
                    >
                      {active && (
                        <Center
                          rounded={"full"}
                          style={{
                            position: "absolute",
                            top: vh(-6),
                            right: vw(-6),
                            backgroundColor: "white",
                          }}
                        >
                          <CheckCircleIcon
                            style={{
                              color: Colors.primary,
                            }}
                          />
                        </Center>
                      )}
                      <HStack
                        style={{
                          margin: vw(20),
                          justifyContent: "space-between",
                        }}
                      >
                        <HStack flex={1} justifyContent={"space-between"}>
                          <VStack>
                            <HStack>
                              <Text
                                style={{
                                  backgroundColor: Colors.primary,
                                  width: vw(60),
                                  height: vh(26),
                                  textAlign: "center",
                                  fontFamily: "Poppins_medium",
                                  fontSize: vw(13),
                                  color: "white",
                                  lineHeight: vh(28),
                                  borderRadius: vw(13),
                                  marginRight: vh(10),
                                  textTransform: "uppercase",
                                }}
                              >
                                {p.type}
                              </Text>
                              <Text
                                style={{
                                  fontFamily: "Poppins_medium",
                                  fontSize: vw(13),
                                  color: "#303030",
                                  lineHeight: vh(28),
                                  height: vh(26),
                                }}
                              >
                                {p.name}
                              </Text>
                            </HStack>
                            <Text
                              style={{
                                fontFamily: "Poppins",
                                fontSize: vw(13),
                                color: !active
                                  ? "rgba(255,255,255, 0.5)"
                                  : "#919191",
                                marginTop: vh(active ? 10 : 30),
                              }}
                            >
                              {active ? rd + " days remaining" : ""}
                            </Text>
                          </VStack>
                          <VStack style={{ marginTop: vh(-10) }}>
                            <Text
                              style={{
                                fontFamily: "Poppins_medium",
                                fontSize: vw(40),
                                color: !active ? "#ffffff" : "#303030",
                              }}
                            >
                              {p.price + "$"}
                            </Text>
                            <Text
                              style={{
                                fontFamily: "Poppins",
                                fontSize: vw(13),
                                color: true
                                  ? "rgba(255,255,255, 0.5)"
                                  : "#919191",
                                textAlign: "center",
                              }}
                            >
                              per month
                            </Text>
                          </VStack>
                        </HStack>
                      </HStack>
                      {!active ? (
                        <HStack
                          style={{
                            borderTopWidth: 1,
                            borderTopColor: "rgba(255,255,255,0.15)",
                            paddingTop: vh(18),
                            marginTop: vh(-18),
                          }}
                          justifyContent={"space-between"}
                        >
                          <Pressable
                            onPress={() => {
                              if (!active) {
                                const { pm } = getUser();
                                if (!pm) {
                                  _showAlert(
                                    "error",
                                    "Add a Payment method before upgrade."
                                  );
                                  return;
                                } else {
                                  setIsConfirm(true);
                                  setSelectedPlan(p);
                                  setIsSubscribed(active);
                                }
                              }
                            }}
                            style={{
                              marginTop: vh(-11),
                              borderColor: "rgba(0,0,0,0.15)",
                              borderWidth: 1,
                              borderRadius: 100,
                              paddingHorizontal: vh(15),
                              paddingVertical: vh(5),
                              background: "white",
                              height: vw(32),
                              marginHorizontal: vw(20),
                              marginVertical: vh(8),
                              // marginTop: vh(!active ? 20 : 20),
                              width: vw(100),
                            }}
                          >
                            <Text
                              style={{
                                color:
                                  // period === "custom"
                                  //   ? Colors.primary
                                  "#666666",
                                fontSize: vw(13),
                                fontFamily: "Poppins_medium",
                                textAlign: "center",
                              }}
                            >
                              {"Upgrade"}
                            </Text>
                          </Pressable>
                          <Pressable
                            onPress={() => {
                              setSelectedPlan(p);
                              setShowDetails(true);
                              setIsSubscribed(active);
                            }}
                          >
                            <Text
                              style={{
                                marginTop: vh(-6),
                                fontFamily: "Poppins",
                                fontSize: vw(13),
                                color: "#FFFFFF",
                                marginHorizontal: vw(20),
                              }}
                            >
                              Learn more{" "}
                              <ChevronRightIcon size={2} color={"white"} />
                            </Text>
                          </Pressable>
                        </HStack>
                      ) : (
                        <HStack
                          style={{
                            borderTopWidth: 1,
                            borderTopColor: "rgba(0,0,0,0.10)",
                            paddingTop: vh(12),
                            marginTop: vh(-18),
                            marginHorizontal: vw(20),
                          }}
                          justifyContent={"space-between"}
                        >
                          <Text
                            style={{
                              color: "#666666",
                              fontSize: vw(13),
                              fontFamily: "Poppins_medium",
                              textAlign: "center",
                            }}
                          >
                            {"Your current plan"}
                          </Text>

                          <Pressable
                            onPress={() => {
                              setSelectedPlan(p);
                              setShowDetails(true);
                              setIsSubscribed(active);
                            }}
                          >
                            <Text
                              style={{
                                marginTop: vh(2),
                                fontFamily: "Poppins",
                                fontSize: vw(13),
                                color: "#686868",
                                // marginHorizontal: vw(20),
                              }}
                            >
                              View Details{" "}
                              <ChevronRightIcon size={2} color={"#686868"} />
                            </Text>
                          </Pressable>
                        </HStack>
                      )}
                    </VStack>
                  );
                })}
          </HStack>
        </Hidden>
      )}

      {/* Small Screens Only */}
      {!showDetails && (
        <Hidden from='lg'>
          <VStack space={4} flex={1} justifyContent='center'>
            {plan &&
              plan
                .filter((p) => {
                  if (profile.plan && profile.plan._id) {
                    return profile.plan.type === p.type;
                  } else {
                    return true;
                  }
                })
                .map((p, i) => {
                  let active = profile.plan._id === p._id;
                  let rd = 0;
                  if (profile.lastsub) {
                    const d = moment.unix(profile.lastsub.end);
                    const n = d.diff(moment(), "days");
                    rd = n;
                    if (profile.lastsub.cancel || n < 0) {
                      active = false;
                    }
                  }

                  return (
                    <Box
                      key={"plan-" + i}
                      style={{
                        flex: 1,
                        borderWidth: vw(2),
                        borderColor: Colors.primary,
                        height: vh(140),
                        borderRadius: 10,
                        boxshadow: "0px 3px 10px #0000001A",
                        backgroundColor: active ? "white" : "transparent",
                        background: !active
                          ? "linear-gradient(137deg, #BA47F3 0%, #7D2BE9 100%)"
                          : "none",
                      }}
                    >
                      {active && (
                        <Center
                          rounded={"full"}
                          style={{
                            position: "absolute",
                            top: vh(-6),
                            right: vw(-6),
                            backgroundColor: "white",
                          }}
                        >
                          <CheckCircleIcon
                            style={{
                              color: Colors.primary,
                            }}
                          />
                        </Center>
                      )}
                      <HStack
                        style={{
                          margin: vw(20),
                          justifyContent: "space-between",
                        }}
                      >
                        <VStack flex={1} justifyContent={"center"}>
                          <VStack>
                            <HStack>
                              <Text
                                style={{
                                  backgroundColor: Colors.primary,
                                  width: vw(60),
                                  height: vh(26),
                                  textAlign: "center",
                                  fontFamily: "Poppins_medium",
                                  fontSize: vw(13),
                                  color: "white",
                                  lineHeight: vh(28),
                                  borderRadius: vw(13),
                                  marginRight: vh(10),
                                  textTransform: "uppercase",
                                }}
                              >
                                {p.type}
                              </Text>
                              <Text
                                style={{
                                  fontFamily: "Poppins_medium",
                                  fontSize: vw(13),
                                  color: "#303030",
                                  lineHeight: vh(28),
                                  height: vh(26),
                                }}
                              >
                                {p.name}
                              </Text>
                            </HStack>
                            <VStack>
                              <Text
                                style={{
                                  fontFamily: "Poppins_medium",
                                  fontSize: vw(40),
                                  color: !active ? "#ffffff" : "#303030",
                                }}
                              >
                                {p.price + "$"}
                              </Text>
                              <Text
                                style={{
                                  fontFamily: "Poppins",
                                  fontSize: vw(13),
                                  marginTop: vh(-10),
                                  color: true
                                    ? "rgba(255,255,255, 0.5)"
                                    : "#919191",
                                }}
                              >
                                per month
                              </Text>
                            </VStack>
                            <Text
                              style={{
                                fontFamily: "Poppins",
                                fontSize: vw(13),
                                color: !active
                                  ? "rgba(255,255,255, 0.5)"
                                  : "#919191",
                                marginTop: vh(active ? -10 : -10),
                              }}
                            >
                              {active ? rd + " days remaining" : ""}
                            </Text>
                            {!active ? (
                              <HStack
                                style={{
                                  borderTopWidth: 1,
                                  borderTopColor: "rgba(255,255,255,0.15)",
                                  paddingTop: vh(20),
                                  marginTop: vh(18),
                                }}
                                justifyContent={"space-between"}
                              >
                                <Pressable
                                  onPress={() => {
                                    if (!active) {
                                      const { pm } = getUser();
                                      if (!pm) {
                                        _showAlert(
                                          "error",
                                          "Add a Payment method before upgrade."
                                        );
                                        return;
                                      } else {
                                        setIsConfirm(true);
                                        setSelectedPlan(p);
                                        setIsSubscribed(active);
                                      }
                                    }
                                  }}
                                  style={{
                                    marginTop: vh(-11),
                                    borderColor: "rgba(0,0,0,0.15)",
                                    borderWidth: 1,
                                    borderRadius: 100,
                                    paddingHorizontal: vh(15),
                                    paddingVertical: vh(5),
                                    background: "white",
                                    height: vw(32),
                                    width: vw(100),
                                  }}
                                >
                                  <Text
                                    style={{
                                      color:
                                        // period === "custom"
                                        //   ? Colors.primary
                                        "#666666",
                                      fontSize: vw(13),
                                      fontFamily: "Poppins_medium",
                                      textAlign: "center",
                                    }}
                                  >
                                    {"Upgrade"}
                                  </Text>
                                </Pressable>
                                <Pressable
                                  onPress={() => {
                                    setSelectedPlan(p);
                                    setShowDetails(true);
                                    setIsSubscribed(active);
                                  }}
                                >
                                  <Text
                                    style={{
                                      marginTop: vh(-6),
                                      fontFamily: "Poppins",
                                      fontSize: vw(13),
                                      color: "#FFFFFF",
                                    }}
                                  >
                                    Learn more{" "}
                                    <ChevronRightIcon
                                      size={2}
                                      color={"white"}
                                    />
                                  </Text>
                                </Pressable>
                              </HStack>
                            ) : (
                              <HStack
                                style={{
                                  borderTopWidth: 1,
                                  borderTopColor: "rgba(0,0,0,0.10)",
                                  paddingTop: vh(12),
                                  marginTop: vh(-18),
                                  marginHorizontal: vw(20),
                                }}
                                justifyContent={"space-between"}
                              >
                                <Text
                                  style={{
                                    color: "#666666",
                                    fontSize: vw(13),
                                    fontFamily: "Poppins_medium",
                                    textAlign: "center",
                                  }}
                                >
                                  {"Your current plan"}
                                </Text>

                                <Pressable
                                  onPress={() => {
                                    setSelectedPlan(p);
                                    setShowDetails(true);
                                    setIsSubscribed(active);
                                  }}
                                >
                                  <Text
                                    style={{
                                      marginTop: vh(2),
                                      fontFamily: "Poppins",
                                      fontSize: vw(13),
                                      color: "#686868",
                                      // marginHorizontal: vw(20),
                                    }}
                                  >
                                    View Details{" "}
                                    <ChevronRightIcon
                                      size={2}
                                      color={"#686868"}
                                    />
                                  </Text>
                                </Pressable>
                              </HStack>
                            )}
                            <ShadowModal
                              size={"lg"}
                              close={() => {
                                setIsConfirm(false);
                                setSelectedPlan(null);
                              }}
                              height={active ? 400 : 300}
                              bodyHeight={20}
                              isOpen={
                                isConfirm &&
                                selectedPlan &&
                                selectedPlan._id === p._id
                              }
                              header={
                                active
                                  ? "Cancel Subscription"
                                  : "Confirm Subscription"
                              }
                              buttons={
                                <HStack space={2}>
                                  <ShadowButton
                                    space={2}
                                    label='Cancel'
                                    variant='outline'
                                    onPress={() => {
                                      setIsConfirm(false);
                                      setSelectedPlan(null);
                                    }}
                                    style={{
                                      background: "white",
                                      height: vw(36),
                                      marginTop: vh(20),
                                    }}
                                  />
                                  <ShadowButton
                                    space={2}
                                    isLoading={isCancel}
                                    label='Proceed'
                                    onPress={async () => {
                                      await cancelSubscription();
                                    }}
                                    style={{
                                      height: vw(36),
                                      marginTop: vh(20),
                                    }}
                                  />
                                </HStack>
                              }
                            >
                              {!active ? (
                                <VStack>
                                  <Text
                                    style={{
                                      fontFamily: "Poppins",
                                      fontSize: vw(15),
                                    }}
                                  >
                                    Are you sure you want to subscribe?
                                  </Text>
                                </VStack>
                              ) : (
                                <VStack>
                                  <Text
                                    style={{
                                      textAlign: "left",
                                      marginBottom: vh(10),
                                      fontSize: vw(13),
                                      fontFamily: "Poppins_medium",
                                    }}
                                  >
                                    You are currently on our {profile.plan.name}{" "}
                                    plan.
                                  </Text>
                                  <Text
                                    style={{
                                      textAlign: "left",
                                      fontSize: vw(13),
                                      fontFamily: "Poppins_medium",
                                    }}
                                  >
                                    If you like to cancel your subscription now,
                                    please press the cancel subscription below
                                    or contact us at support@shadow.properties
                                    if you'd like to discuss your account.
                                  </Text>
                                </VStack>
                              )}
                            </ShadowModal>
                          </VStack>
                        </VStack>
                      </HStack>
                    </Box>
                  );
                })}
          </VStack>
        </Hidden>
      )}

      {/* <VStack style={{ marginTop: vh(34) }}>
      <HStack justifyContent={"space-between"}>
        <Text
          style={{
            fontSize: vw(13),
            fontFamily: "Poppins_medium",
            color: "#303030",
          }}
        >
          Enable auto smart search
        </Text>
        <Switch
          value={getValues("autosmartsearch") || false}
          onValueChange={(v) => {
            setValue("autosmartsearch", v);
          }}
          isChecked={getValues("autosmartsearch") || false}
          onTrackColor={Colors.primary}
          size={"sm"}
        />
      </HStack>
      <Text
        style={{
          fontSize: vw(13),
          fontFamily: "Poppins",
          color: "#919191",
        }}
      >
        Es un hecho establecido hace demasiado tiempo que un lector se
        distraer con el contenido del texto de un sitio mientras que mira
        su diseo. El punto de usar Lorem Ipsum es que tiene una
        distribucin ms o menos normal de las letras, al contrario de usar
        textos como por ejemplo
      </Text>
    </VStack> */}

      <ShadowModal
        placement={"top"}
        size={"lg"}
        close={() => {
          setIsConfirm(false);
          setSelectedPlan(null);
        }}
        // height={active ? 400 : 300}
        // bodyHeight={20}
        isOpen={isConfirm && selectedPlan}
        header={isSubscribed ? "Cancel Subscription" : "Confirm Subscription"}
        buttons={
          <HStack space={2}>
            <ShadowButton
              space={2}
              label='Cancel'
              variant='outline'
              onPress={() => {
                setIsConfirm(false);
              }}
              style={{
                background: "white",
                height: vw(36),
                marginTop: vh(20),
              }}
            />
            <ShadowButton
              space={2}
              isLoading={isSubscribed ? isCancel : isCreating}
              label='Proceed'
              onPress={async () => {
                isSubscribed
                  ? await cancelSubscription()
                  : await createSubscription();
              }}
              style={{
                height: vw(36),
                marginTop: vh(20),
              }}
            />
          </HStack>
        }
      >
        {/* !active && !subCreated ? */}
        {!isSubscribed ? (
          <VStack>
            <Text
              style={{
                fontFamily: "Poppins",
                fontSize: vw(15),
              }}
            >
              Are you sure you want to subscribe?
            </Text>
          </VStack>
        ) : (
          <VStack>
            <Text
              style={{
                textAlign: "left",
                marginBottom: vh(10),
                fontSize: vw(13),
                fontFamily: "Poppins_medium",
              }}
            >
              You are currently on our {profile.plan.name} plan.
            </Text>
            <Text
              style={{
                textAlign: "left",
                fontSize: vw(13),
                fontFamily: "Poppins_medium",
              }}
            >
              If you like to cancel your subscription now, please press the
              proceed button below or contact us at support@shadow.properties if
              you'd like to discuss your account.
            </Text>
          </VStack>
        )}
      </ShadowModal>
    </VStack>
  );
};

export default SubscriptionPlans;
